import { createRouter, createWebHistory } from "@ionic/vue-router";
import TabsPage from "../views/TabsPage.vue";
import Homepage2 from "@/views/HomePage2.vue";
import RegisterPage from "../views/RegisterPage.vue";
import AdminPage from "@/views/AdminPage.vue";
import GuruPage from "@/views/GuruPage.vue";
import SiswaPage from "@/views/SiswaPage.vue";

const routes = [
  {
    path: "/",
    redirect: "/page",
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/LoginPage.vue"),
  },
  {
    path: "/splash",
    name: "splash",
    component: () => import("@/views/SplashScreen.vue"),
  },
  {
    path: "/page/",
    component: TabsPage,
    children: [
      {
        path: "",
        redirect: "/page/home",
      },
      {
        path: "home",
        name: "home",
        component: () => import("@/views/HomePage2.vue"),
      },
      {
        path: "/siswa",
        name: "siswa",
        component: SiswaPage,
      },
      {
        path: "/register",
        name: "register",
        component: RegisterPage,
      },
      {
        path: "/admin",
        name: "admin",
        component: AdminPage,
      },
      {
        path: "/guru",
        name: "guru",
        component: GuruPage,
      },
      {
        path: "homepage2",
        name: "homepage2",
        component: Homepage2,
      },
      {
        path: "soalsoal/:menuId",
        name: "soalsoal",
        component: () => import("@/views/SoalSoal/SoalSoal.vue"),
      },
    ],
  },
  
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
